import {
  AcceptButtonStyle,
  DeclineButtonStyle,
  IconStyle,
  IncomingCallButtonsStyle,
  IncomingCallLabelStyle,
  IncomingCallListItemStyle,
  IncomingCallSubtitleStyle,
  IncomingCallTailViewStyle,
  IncomingCallWrapperStyle,
  defaultAvatarStyle,
  defaultIncomingCallStyle,
  defaultListItemStyle,
  defaultOngoingCallStyle,
} from "./style";
import { AvatarStyle, ListItemStyle } from "@cometchat/uikit-elements";
import {
  CometChatCallEvents,
  CometChatUIKitConstants,
  localize,
} from "@cometchat/uikit-resources";
import {
  CometChatSoundManager,
  CometChatUIKitCalls,
  IncomingCallStyle,
  StorageUtils,
} from "@cometchat/uikit-shared";
import { useCallback, useContext, useRef, useState } from "react";

import AudioCallIcon from "./assets/Audio-Call.svg";
import { CometChat } from "@cometchat/chat-sdk-javascript";
import { CometChatOngoingCall } from "../CometChatOngoingCall";
import { CometChatThemeContext } from "../../CometChatThemeContext";
import { Hooks } from "./hooks";
import VideoCallIcon from "./assets/Video-call.svg";

interface IIncomingCallProps {
  call?: any;
  disableSoundForCalls?: boolean;
  customSoundForCalls?: string;
  onAccept?: Function;
  onDecline?: Function;
  acceptButtonText?: string;
  declineButtonText?: string;
  subtitleView?: any;
  onError?: Function;
  listItemStyle?: ListItemStyle;
  avatarStyle?: AvatarStyle;
  incomingCallStyle?: IncomingCallStyle;
}

const CometChatIncomingCall = (props: IIncomingCallProps) => {
  const {
    call,
    disableSoundForCalls = false,
    customSoundForCalls = "",
    onAccept,
    onDecline,
    acceptButtonText = localize("ACCEPT"),
    declineButtonText = localize("DECLINE"),
    subtitleView = null,
    listItemStyle = {
      height: "100%",
      width: "100%",
    },
    avatarStyle = {
      borderRadius: "16px",
      width: "38px",
      height: "38px",
    },
    incomingCallStyle = {
      width: "fit-content",
      height: "fit-content",
    },
    onError = (error: CometChat.CometChatException) => {
      console.log(error);
    },
  } = props;

  const { theme } = useContext(CometChatThemeContext);

  const [loggedInUser, setLoggedInuser] = useState<CometChat.User | null>(null);
  const [showIncomingCallScreen, setShowIncomingCallScreen] = useState(false);
  const [showOngoingCallScreen, setShowOngoingCallScreen] = useState(false);
  const [showOutGoingCallScreen, setShowOutGoingCallScreen] = useState(false);

  const listItemStyleRef = useRef<ListItemStyle | null>(null);
  const avatarStyleRef = useRef<AvatarStyle | null>(null);
  const incomingCallStyleRef = useRef<IncomingCallStyle | null>(null);
  const callRef = useRef<CometChat.Call | null>(null);
  const sessionIdRef = useRef("");
  const rejectCallButtonRef = useRef(null);
  const acceptCallButtonRef = useRef(null);

  avatarStyleRef.current = { ...defaultAvatarStyle(theme), ...avatarStyle };

  incomingCallStyleRef.current = {
    ...defaultIncomingCallStyle(theme),
    ...incomingCallStyle,
  };

  listItemStyleRef.current = {
    ...defaultListItemStyle(incomingCallStyleRef?.current, theme),
    ...listItemStyle,
  };

  const acceptButtonStyleRef = useRef({});

  acceptButtonStyleRef.current = AcceptButtonStyle(
    incomingCallStyleRef?.current
  );

  const declineButtonStyleRef = useRef({});

  declineButtonStyleRef.current = DeclineButtonStyle(
    incomingCallStyleRef?.current
  );

  const ongoingCallStyleRef = useRef({});
  ongoingCallStyleRef.current = { ...defaultOngoingCallStyle(theme) };

  const iconStyleRef = useRef<any>(null);

  iconStyleRef.current = IconStyle(incomingCallStyleRef?.current);

  let incomingcallListenerId: string = "incomingcall_" + new Date().getTime(),
    subtitleText: string = localize("INCOMING_CALL");

  const onErrorCallback = useCallback(
    (error: any) => {
      if (!(error instanceof CometChat.CometChatException)) {
        let errorModel = {
          code: error?.code,
          name: error?.name,
          message: error?.message,
          details: error?.details,
        };
        let errorObj = new CometChat.CometChatException(errorModel);
        onError!(errorObj);
      } else {
        onError!(error);
      }
    },
    [onError]
  );

  const playAudio = useCallback(() => {
    try {
      if (customSoundForCalls) {
        CometChatSoundManager.play(
          CometChatSoundManager.Sound.incomingCall,
          customSoundForCalls
        );
      } else {
        CometChatSoundManager.play(CometChatSoundManager.Sound.incomingCall);
      }
    } catch (e) {
      onErrorCallback(e);
    }
  }, [customSoundForCalls, onErrorCallback]);

  const isCallActive = useCallback(
    (call: CometChat.Call) => {
      let isCurrentCall: boolean = false;
      try {
        if (StorageUtils.getItem(CometChatUIKitConstants.calls.activecall)) {
          let oldCall: any = StorageUtils.getItem(
            CometChatUIKitConstants.calls.activecall
          );
          if (oldCall && oldCall.sessionId === call.getSessionId()) {
            isCurrentCall = true;
          } else {
            isCurrentCall = false;
          }
        } else {
          isCurrentCall = false;
        }
        return isCurrentCall;
      } catch (e) {
        onErrorCallback(e);
        return isCurrentCall;
      }
    },
    [onErrorCallback]
  );

  const rejectIncomingCall = useCallback(
    (reason: string = CometChatUIKitConstants.calls.rejected) => {
      try {
        CometChatSoundManager.pause();
        if (onDecline) {
          onDecline();
        } else if (typeof callRef?.current?.getSessionId() === "string") {
          CometChat.rejectCall(callRef?.current?.getSessionId(), reason).then(
            (rejectedCall: CometChat.Call) => {
              StorageUtils.setItem(
                CometChatUIKitConstants.calls.activecall,
                rejectedCall
              );
              CometChatCallEvents.ccCallRejected.next(rejectedCall);
              setShowIncomingCallScreen(false);
              callRef.current = null;
            },
            (error: CometChat.CometChatException) => {
              onErrorCallback(error);
            }
          );
        }
      } catch (e) {
        onErrorCallback(e);
      }
    },
    [onDecline, onErrorCallback]
  );

  const showCall = useCallback(
    (call: CometChat.Call) => {
      try {
        if (
          !isCallActive(call) &&
          loggedInUser?.getUid() !== call?.getSender()?.getUid() &&
          callRef.current
        ) {
          if (
            !disableSoundForCalls &&
            !showOngoingCallScreen &&
            !showOutGoingCallScreen
          ) {
            setTimeout(() => {
              playAudio();
            }, 100);
          }
          if (!showOngoingCallScreen && !showOutGoingCallScreen) {
            setShowIncomingCallScreen(true);
          } else if (showOngoingCallScreen || showOutGoingCallScreen) {
            CometChatSoundManager.pause();
            rejectIncomingCall(CometChatUIKitConstants.calls.busy);
            CometChatCallEvents.ccCallRejected.next(call);
          }
        } else if (loggedInUser?.getUid() === call?.getSender()?.getUid()) {
          CometChatSoundManager.pause();
          return;
        } else {
          CometChatSoundManager.pause();
          rejectIncomingCall(CometChatUIKitConstants.calls.busy);
        }
      } catch (e) {
        onErrorCallback(e);
      }
    },
    [
      isCallActive,
      disableSoundForCalls,
      playAudio,
      rejectIncomingCall,
      onErrorCallback,
      loggedInUser,
      showOngoingCallScreen,
      showOutGoingCallScreen,
    ]
  );

  const localStorageChange = useCallback((event: any) => {
    if (event?.key !== CometChatUIKitConstants.calls.activecall) {
      return;
    }
    if (event.newValue || event.oldValue) {
      let call;
      if (event.newValue) {
        call = JSON.parse(event.newValue);
      } else if (event.oldValue) {
        call = JSON.parse(event.oldValue);
      }
      if (callRef.current?.getSessionId() === call?.sessionId) {
        CometChatSoundManager.pause();
        callRef.current = null;
        setShowIncomingCallScreen(false);
      }
    }
    return;
  }, []);

  const closeCallScreen = () => {
    setShowOngoingCallScreen(false);
    setShowOutGoingCallScreen(false)
    callRef.current = null;
    sessionIdRef.current = "";
  };

  const subscribeToEvents = useCallback(() => {
    try {
      const ccCallEnded = CometChatCallEvents.ccCallEnded.subscribe(
        (call: CometChat.Call) => {
          closeCallScreen();
        }
        
      );

      const ccOutgoingCall = CometChatCallEvents.ccOutgoingCall.subscribe(
        () => {
          setShowOutGoingCallScreen(true);
        }
      );

      return () => {
        try {
          ccCallEnded?.unsubscribe();
          ccOutgoingCall?.unsubscribe();
        } catch (error: any) {
          onErrorCallback(error);
        }
      };
    } catch (e) {
      onErrorCallback(e);
    }
  }, [onErrorCallback]);

  const attachListeners = useCallback(() => {
    try {
      StorageUtils.attachChangeDetection(localStorageChange);
      CometChat.addCallListener(
        incomingcallListenerId,
        new CometChat.CallListener({
          onIncomingCallReceived: (call: CometChat.Call) => {
            if (callRef.current?.getSender()?.getUid() === call.getSender()?.getUid()){
              return;
            }
            callRef.current = call;
            showCall(call);
          },
          onIncomingCallCancelled: (call: CometChat.Call) => {
            CometChatSoundManager.pause();
            callRef.current = null;
            setShowIncomingCallScreen(false);
          },
          onOutgoingCallAccepted: (call: CometChat.Call) => {
            CometChatSoundManager.pause();
            if (call.getSender()?.getUid() === loggedInUser?.getUid()) {
            callRef.current = null;
            setShowIncomingCallScreen(false);
            }
          },
          onOutgoingCallRejected: (call: CometChat.Call) => {
            CometChatSoundManager.pause();
            if (call.getSender()?.getUid() === loggedInUser?.getUid()){
            callRef.current = null;
            setShowIncomingCallScreen(false);
            }
          }
        })
      );
    } catch (e) {
      onErrorCallback(e);
    }
  }, [localStorageChange, showCall, onErrorCallback, incomingcallListenerId,loggedInUser]);

  const removeListener = useCallback(() => {
    try {
      StorageUtils.detachChangeDetection(localStorageChange);
      CometChat.removeCallListener(incomingcallListenerId);
    } catch (e) {
      onErrorCallback(e);
    }
  }, [localStorageChange, onErrorCallback, incomingcallListenerId]);

  const checkForActiveCallAndEndCall = useCallback(() => {
    try {
      let call: CometChat.Call = CometChat.getActiveCall();
      return new Promise((resolve, reject) => {
        if (!call) {
          return resolve({ success: true });
        }
        let sessionID = call?.getSessionId();
        CometChat.endCall(sessionID).then(
          (response: CometChat.Call | null) => {
            return resolve(response);
          },
          (error: CometChat.CometChatException) => {
            return reject(error);
          }
        );
      });
    } catch (e) {
      onErrorCallback(e);
    }
  }, [onErrorCallback]);

  const acceptIncomingCall = useCallback(() => {
    try {
      CometChatSoundManager.pause();
      if (onAccept) {
        onAccept();
      } else {
        checkForActiveCallAndEndCall()?.then(
          (response) => {
            CometChat.acceptCall(callRef.current!.getSessionId()).then(
              (call: CometChat.Call) => {
                CometChatCallEvents.ccCallAccepted.next(call);
                StorageUtils.setItem(
                  CometChatUIKitConstants.calls.activecall,
                  call
                );
                setShowOngoingCallScreen(true);
                callRef.current = call;
                sessionIdRef.current = call?.getSessionId();
                setShowIncomingCallScreen(false);
              },
              (error: CometChat.CometChatException) => {
                onErrorCallback(error);
              }
            );
          },
          (error: CometChat.CometChatException) => {
            onErrorCallback(error);
          }
        );
      }
    } catch (e) { }
  }, [checkForActiveCallAndEndCall, onErrorCallback, onAccept]);

  function getCallBuilder(): typeof CometChatUIKitCalls.CallSettings {
    let audioOnlyCall: boolean =
      callRef.current?.getType() === CometChatUIKitConstants.MessageTypes.audio
        ? true
        : false;
    if (callRef.current?.getType() === CometChatUIKitConstants.calls.meeting) {
      return undefined;
    }

    const callSettings = new CometChatUIKitCalls.CallSettingsBuilder()
      .enableDefaultLayout(true)
      .setIsAudioOnlyCall(audioOnlyCall)
      .setCallListener(
        new CometChatUIKitCalls.OngoingCallListener({
          onCallEnded: () => {
            if (
              callRef.current?.getReceiverType() ===
              CometChatUIKitConstants.MessageReceiverType.user
            ) {
              CometChat.endCall(sessionIdRef.current)
              .then((call: CometChat.Call) => {
                CometChatUIKitCalls.endSession();
                CometChatCallEvents.ccCallEnded.next(null as any);
                closeCallScreen();
              })
              .catch((err: CometChat.CometChatException) => { });
            }
          },
          onCallEndButtonPressed: () => {
            CometChat.endCall(sessionIdRef.current)
              .then((call: CometChat.Call) => {
                CometChatUIKitCalls.endSession();
                CometChatCallEvents.ccCallEnded.next(call);
              })
              .catch((err: CometChat.CometChatException) => { });
          },
          onError: (error: any) => {
            onErrorCallback(error);
          },
        })
      ).build();
    return callSettings;
  }

  const getCallTypeIcon = () => {
    if (
      callRef.current?.getType() === CometChatUIKitConstants.MessageTypes.audio
    ) {
      return AudioCallIcon;
    } else {
      return VideoCallIcon;
    }
  };

  Hooks(
    loggedInUser,
    setLoggedInuser,
    call,
    subscribeToEvents,
    attachListeners,
    removeListener,
    acceptCallButtonRef,
    rejectCallButtonRef,
    showCall,
    callRef,
    acceptIncomingCall,
    rejectIncomingCall,
    showIncomingCallScreen
  );
  return (
    <>
      {callRef.current && showIncomingCallScreen ? (
        <div
          className="cc-incomingcall__wrapper"
          style={IncomingCallWrapperStyle(incomingCallStyleRef?.current)}
        >
          <div
            className="cc-incomingcall__listitem"
            style={IncomingCallListItemStyle}
          >
            <cometchat-list-item
              title={callRef.current?.getSender()?.getName()}
              listItemStyle={JSON.stringify(listItemStyleRef.current)}
              hideSeparator={true}
            >
              {subtitleView ? (
                <div
                  slot="subtitleView"
                  className="cc-incomingcall__subtitleview"
                >
                  {subtitleView}
                </div>
              ) : (
                <div
                  slot="subtitleView"
                  className="cc-incomingcall__subtitleview"
                  style={IncomingCallSubtitleStyle}
                >
                  <div className="cc-call__icon">
                    <cometchat-icon
                      iconStyle={JSON.stringify(iconStyleRef.current)}
                      URL={getCallTypeIcon()}
                    ></cometchat-icon>
                  </div>
                  <cometchat-label
                    className="cc-call__type"
                    text={subtitleText}
                    labelStyle={JSON.stringify(
                      IncomingCallLabelStyle(incomingCallStyleRef?.current)
                    )}
                  ></cometchat-label>
                </div>
              )}

              <div
                slot="tailView"
                className="cc-incomingcall__tailview"
                style={IncomingCallTailViewStyle}
              >
                <div className="tail__view">
                  <div className="cc-incomingcall__avatar">
                    <cometchat-avatar
                      avatarStyle={JSON.stringify(avatarStyleRef.current)}
                      image={callRef.current?.getSender()?.getAvatar()}
                      name={callRef.current?.getSender()?.getName()}
                    ></cometchat-avatar>
                  </div>
                </div>
              </div>
            </cometchat-list-item>
          </div>
          <div
            className="cc-incomingcall-buttons"
            style={IncomingCallButtonsStyle}
          >
            <cometchat-button
              ref={rejectCallButtonRef}
              buttonStyle={JSON.stringify(declineButtonStyleRef.current)}
              text={declineButtonText}
            ></cometchat-button>
            <cometchat-button
              ref={acceptCallButtonRef}
              buttonStyle={JSON.stringify(acceptButtonStyleRef.current)}
              text={acceptButtonText}
            ></cometchat-button>
          </div>
        </div>
      ) : null}

      {showOngoingCallScreen && callRef.current && !showIncomingCallScreen ? (
        <CometChatOngoingCall
          ongoingCallStyle={ongoingCallStyleRef.current}
          sessionID={sessionIdRef.current}
          callSettingsBuilder={getCallBuilder()}
        ></CometChatOngoingCall>
      ) : null}
    </>
  );
};

export { CometChatIncomingCall };
