import {
  AIOptionsStyle,
  CreatePollStyle,
  MessageComposerStyle,
} from "@cometchat/uikit-shared";
import {
  ActionSheetStyle,
  EmojiKeyboardStyle,
  MediaRecorderStyle,
  PopoverStyle,
  PreviewStyle,
  TextInputStyle,
} from "@cometchat/uikit-elements";
import { CometChatTheme, fontHelper } from "@cometchat/uikit-resources";

import { CSSProperties } from "react";

export const LIVE_REACTION_ICON_TINT = "red";
const RIGHT_MOST_BUTTON_HORIZONTAL_MARGIN = "10px";

export type mentionIconStyleType = CSSProperties | { buttonTextFont: string, buttonTextColor: string, buttonIconTint: string, iconBackground: string, iconHeight: string, iconWidth: string, };



export function getMessageComposerStyle(messageComposerStyle: MessageComposerStyle | undefined, theme: CometChatTheme): CSSProperties {
  return {
    width: messageComposerStyle?.width || "100%",
    height: messageComposerStyle?.height || "100%",
    boxSizing: "border-box",
    padding: "14px 16px",
    border: "none",
    borderRadius: "0",
    background: "transparent",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-end",
  };
}
export function previewStyle(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme
): PreviewStyle {
  return new PreviewStyle({
    height: "100%",
    width: "100%",
    border: `1px solid ${theme.palette.getAccent200()}`,
    background: theme.palette.getBackground(),
    previewTitleFont:
      messageComposerStyle?.previewTitleFont ||
      fontHelper(theme.typography.caption1),
    previewTitleColor:
      messageComposerStyle?.previewTitleColor || theme.palette.getAccent600(),
    previewSubtitleFont:
      messageComposerStyle?.previewSubtitleFont ||
      fontHelper(theme.typography.subtitle2),
    previewSubtitleColor:
      messageComposerStyle?.previewSubtitleColor ||
      theme.palette.getAccent400(),
    closeButtonIconTint:
      messageComposerStyle?.closePreviewTint || theme.palette.getAccent600(),
    borderRadius: '12px'
  });
}



export function getMentionsViewStyle(): CSSProperties {
  return {
    maxHeight: '196px',
    overflowY: 'hidden',
    display: "flex !important",
    flexDirection: 'column',
    justifyContent: 'flex-end !important',
    zIndex: 2,
    height: "auto",
    minHeight: '45px',
    padding: '0px 0px 1px 0px',
    width: '100%',
  };
}

export function getMentionInfoIconStyle(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  mentionsWarningStyle: React.CSSProperties
): mentionIconStyleType {
  return {
    height: "fit-content",
    width: "fit-content",
    buttonTextFont: fontHelper(theme.typography.subtitle2),
    buttonTextColor: theme.palette.getAccent600(),
    borderRadius: "8px",
    border: "none",
    buttonIconTint: theme.palette.getAccent600(),
    padding: "8px",
    iconHeight: "20px",
    iconWidth: "20px",
    iconBackground: "transparent",
    gap: "5px",
    ...mentionsWarningStyle,
  };
}

export function textInputStyle(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme
): TextInputStyle {
  return new TextInputStyle({
    height: "100%",
    width: "100%",
    maxHeight: messageComposerStyle?.maxInputHeight || "150px",
    border:
      messageComposerStyle?.border ||
      `1px solid ${theme.palette.getAccent100()}`,
    borderRadius: messageComposerStyle?.borderRadius || "12px",
    background:
      messageComposerStyle?.background || `${theme.palette.getAccent50()}`,
    textFont:
      messageComposerStyle?.textFont || fontHelper(theme.typography.text2),
    textColor: messageComposerStyle?.textColor || theme.palette.getAccent(),
    dividerColor:
      messageComposerStyle?.dividerTint || theme.palette.getAccent100(),
    inputBorder: messageComposerStyle?.inputBorder || "none",
    inputBorderRadius: messageComposerStyle?.inputBorderRadius || "0px",
    inputBackground: messageComposerStyle?.inputBackground || "transparent",
    placeholderTextColor: theme.palette.getAccent500(),
    placeholderTextFont: fontHelper(theme.typography.subtitle2)
  });
}

export function liveReactionButtonStyle(theme: CometChatTheme, style?: MessageComposerStyle) {
  return {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "0",
    buttonIconTint: style?.liveReactionIconTint || "red",
    background: "transparent",
    padding: "0",
  };
}

export function sendButtonStyle(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isDisabled: boolean = false
) {

  const buttonTint = isDisabled ? theme.palette.getAccent200() : (messageComposerStyle?.sendIconTint || theme.palette.getPrimary())

  return {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "0",
    buttonIconTint: buttonTint,
    background: "transparent",
    padding: "0",
  };
}

function getEmojiButtonIconTint(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isAuxiliaryContentDisplayed: boolean
): string | undefined {
  if (isAuxiliaryContentDisplayed) {
    return messageComposerStyle?.emojiIconTint || theme.palette.getAccent();
  }

  return messageComposerStyle?.emojiIconTint || theme.palette.getAccent500();
}

function getVoiceButtonIconTint(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isAuxiliaryContentDisplayed: boolean
): string | undefined {
  if (isAuxiliaryContentDisplayed) {
    return messageComposerStyle?.voiceRecordingIconTint || theme.palette.getAccent();
  }
  return messageComposerStyle?.voiceRecordingIconTint || theme.palette.getAccent500();
}

function getAttachButtonIconTint(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isSecondaryContentDisplayed: boolean
): string | undefined {
  if (isSecondaryContentDisplayed) {
    return messageComposerStyle?.attachIcontint || theme.palette.getAccent();
  }

  return messageComposerStyle?.attachIcontint || theme.palette.getAccent500();
}

function getAIButtonIconTint(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isSecondaryContentDisplayed: boolean
): string | undefined {
  if (isSecondaryContentDisplayed) {
    return messageComposerStyle?.AIIconTint || theme.palette.getAccent();
  }

  return messageComposerStyle?.AIIconTint || theme.palette.getAccent500();
}

