const VOICE_RECORDING_HEIGHT = "100px";
const VOICE_RECORDING_WIDTH = "250px";
const VOICE_RECORDING_BORDER_RADIUS = "8px";

export function voiceRecordingPopoverStyle(): PopoverStyle {
  return new PopoverStyle({
    width: VOICE_RECORDING_WIDTH,
    height: VOICE_RECORDING_HEIGHT,
    borderRadius: VOICE_RECORDING_BORDER_RADIUS,
    border: "1px solid rgba(20, 20, 20, 0.08)",
    boxShadow:
      "0px 0px 0px 1px rgba(20, 20, 20, 0.04), 0px 16px 32px 0px rgba(20, 20, 20, 0.2)",
  });
}

export function voiceRecordingContainerStyle(
  theme: CometChatTheme
): CSSProperties {
  return {
    width: VOICE_RECORDING_WIDTH,
    height: VOICE_RECORDING_HEIGHT,
    // maxWidth: "80vw",
    borderRadius: VOICE_RECORDING_BORDER_RADIUS,
    boxSizing: "border-box",
  };
}

export function voiceRecordingStyle(
  mediaRecorderStyle: MediaRecorderStyle | undefined,
  theme: CometChatTheme
): EmojiKeyboardStyle {
  return new MediaRecorderStyle({
    ...mediaRecorderStyle,
    background: mediaRecorderStyle?.background || theme.palette.getBackground(),
    border: "1px solid rgba(20, 20, 20, 0.08)",
    borderRadius: VOICE_RECORDING_BORDER_RADIUS,
    closeIconTint:
      mediaRecorderStyle?.closeIconTint || theme.palette.getAccent600(),
    submitIconTint:
      mediaRecorderStyle?.submitIconTint || theme.palette.getAccent600(),
    startIconTint:
      mediaRecorderStyle?.startIconTint || theme.palette.getError(),
    stopIconTint: mediaRecorderStyle?.stopIconTint || theme.palette.getError(),
    timerTextColor: mediaRecorderStyle?.timerTextColor || theme.palette.getAccent400(),
    timerTextFont: mediaRecorderStyle?.timerTextFont || fontHelper(theme.typography.text3),
  });
}

export function voiceButtonStyle(
  messageComposerStyle: MessageComposerStyle | undefined,
  theme: CometChatTheme,
  isAuxiliaryContentDisplayed: boolean,
  applyHorizontalMargin: boolean = false
) {
  const marginStyle: { margin?: string } = {};

  if (applyHorizontalMargin) {
    marginStyle.margin = `0px ${RIGHT_MOST_BUTTON_HORIZONTAL_MARGIN}`;
  }

  return {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "0",
    buttonIconTint: getVoiceButtonIconTint(
      messageComposerStyle,
      theme,
      isAuxiliaryContentDisplayed
    ),
    background: "transparent",
    ...marginStyle,
  };
}

export function defaultAuxiliaryViewContainer(): CSSProperties {
  return {
    display: "flex",
    flex: "1",
  };
}

