  getAllMessageTemplates(
    theme?: CometChatTheme,
    additionalConfigurations?: any
  ): Array<CometChatMessageTemplate> {
    let _theme: CometChatTheme = theme ?? defaultTheme;
    if (!additionalConfigurations) {
      additionalConfigurations = {
        disableMentions: false,
      };
    }
    return [
      ChatConfigurator.getDataSource().getTextMessageTemplate(
        _theme,
        additionalConfigurations
      ),
      ChatConfigurator.getDataSource().getImageMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getVideoMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getAudioMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getFileMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getGroupActionTemplate(_theme),
      ChatConfigurator.getDataSource().getFormMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getSchedulerMessageTemplate(_theme),
      ChatConfigurator.getDataSource().getCardMessageTemplate(_theme),
    ];
  }

  getMessageTemplate(
    messageType: string,
    messageCategory: string,
    theme?: CometChatTheme,
    additionalConfigurations?: any
  ): CometChatMessageTemplate | null {
    let _theme: CometChatTheme = theme ?? defaultTheme;

    if (!additionalConfigurations) {
      additionalConfigurations = {
        disableMentions: false,
      };
    }

    let _template: CometChatMessageTemplate | null = null;
    if (messageCategory !== CometChatUIKitConstants.MessageCategory.call) {
      switch (messageType) {
        case CometChatUIKitConstants.MessageTypes.text:
          _template =
            ChatConfigurator.getDataSource().getTextMessageTemplate(_theme, additionalConfigurations);
          break;

        case CometChatUIKitConstants.MessageTypes.image:
          _template =
            ChatConfigurator.getDataSource().getImageMessageTemplate(_theme);
          break;

        case CometChatUIKitConstants.MessageTypes.video:
          _template =
            ChatConfigurator.getDataSource().getVideoMessageTemplate(_theme);
          break;

        case CometChatUIKitConstants.MessageTypes.groupMember:
          _template =
            ChatConfigurator.getDataSource().getGroupActionTemplate(_theme);
          break;

        case CometChatUIKitConstants.MessageTypes.file:
          _template =
            ChatConfigurator.getDataSource().getFileMessageTemplate(_theme);
          break;

        case CometChatUIKitConstants.MessageTypes.audio:
          _template =
            ChatConfigurator.getDataSource().getAudioMessageTemplate(_theme);
          break;
        case CometChatUIKitConstants.MessageTypes.form:
          _template =
            ChatConfigurator.getDataSource().getFormMessageTemplate(_theme);
          break;
        case CometChatUIKitConstants.MessageTypes.scheduler:
          _template =
            ChatConfigurator.getDataSource().getSchedulerMessageTemplate(
              _theme
            );
          break;
        case CometChatUIKitConstants.MessageTypes.card:
          _template =
            ChatConfigurator.getDataSource().getCardMessageTemplate(_theme);
          break;
      }
    }
    return _template;
  }

  getMessageOptions(
    loggedInUser: CometChat.User,
    messageObject: CometChat.BaseMessage,
    theme: CometChatTheme,
    group?: CometChat.Group
  ): Array<CometChatActionsIcon | CometChatActionsView> {
    let _optionList: Array<CometChatActionsIcon | CometChatActionsView> = [];

    if (
      messageObject.getCategory() ===
      CometChatUIKitConstants.MessageCategory.message
    ) {
      switch (messageObject.getType()) {
        case CometChatUIKitConstants.MessageTypes.text:
          _optionList = ChatConfigurator.getDataSource().getTextMessageOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
        case CometChatUIKitConstants.MessageTypes.image:
          _optionList = ChatConfigurator.getDataSource().getImageMessageOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
        case CometChatUIKitConstants.MessageTypes.video:
          _optionList = ChatConfigurator.getDataSource().getVideoMessageOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
        case CometChatUIKitConstants.MessageTypes.groupMember:
          _optionList = [];
          break;
        case CometChatUIKitConstants.MessageTypes.file:
          _optionList = ChatConfigurator.getDataSource().getFileMessageOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
        case CometChatUIKitConstants.MessageTypes.audio:
          _optionList = ChatConfigurator.getDataSource().getAudioMessageOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
        default:
          _optionList = ChatConfigurator.getDataSource().getCommonOptions(
            loggedInUser,
            messageObject,
            theme,
            group
          );
          break;
      }
    }
    return _optionList;
  }

  getCommonOptions(
    loggedInUser: CometChat.User,
    messageObject: CometChat.BaseMessage,
    theme: CometChatTheme,
    group?: CometChat.Group
  ): Array<CometChatActionsIcon | CometChatActionsView> {
    let isSentByMe: boolean = this.isSentByMe(loggedInUser, messageObject);
    let isParticipant: boolean = false;
    if (group?.getScope() === CometChatUIKitConstants.groupMemberScope.participant)
      isParticipant = true;

    let messageOptionList: Array<CometChatActionsIcon | CometChatActionsView> =
      [];

    messageOptionList.push(this.getReactionOption(theme));

    if (!messageObject?.getParentMessageId()) {
      messageOptionList.push(this.getReplyInThreadOption(theme));
    }
    if (isSentByMe) {
      messageOptionList.push(this.getMessageInfoOption(theme));
    }
    if (isSentByMe || (!isParticipant && group))
      messageOptionList.push(this.getDeleteOption(theme));

    if (group?.getGuid() && !isSentByMe) {
      messageOptionList.push(this.getSendMessagePrivatelyOption(theme));
    }

    return messageOptionList;
  }

