  getFormMessageBubble(
    message: FormMessage,
    theme: CometChatTheme,
    style?: any,
    onSubmitClick?: Function
  ): any {
    let defaultStyle: any = style;
    if (!defaultStyle) {
      defaultStyle = this.getFormMessageBubbleStyle(theme);
    }

    const hasCustomCallback = onSubmitClick ? true : false;

    return (
      <FormMessageBubble
        message={message}
        formBubbleStyle={defaultStyle}
        ccSubmitClicked={(e: any) => {
          if (onSubmitClick) onSubmitClick(e.detail);
        }}
        hasCustomCallback={hasCustomCallback}
      />
    );
  }
  getSchedulerMessageBubble(
    message: SchedulerMessage,
    theme: CometChatTheme,
    style?: any,
    onSubmitClick?: (timestamp: string, message: SchedulerMessage) => void
  ): any {
    let defaultStyle: any = style;
    if (!defaultStyle) {
      defaultStyle = this.getSchedulerBubbleStyle(theme);
    }

    return (
      <SchedulerMessageBubble
        style={this.getSchedulerWrapperStyle()}
        schedulerMessage={message}
        schedulerBubbleStyle={defaultStyle}
        onScheduleClick={onSubmitClick ? onSubmitClick : undefined}
      />
    );
  }
